import React from "react";
import { Form } from "react-bootstrap";
import {
  IDownloadSection,
  IFeature,
  IGenericText,
} from "../../../interfaces";
import DownloadSections from "./download-sections";
import FeaturesSection from "./features";
import FooterParagraphs from "./footer-paragraphs";

function SimpleTemplateForm({
  footerParagraphs,
  setFooterParagraphs,
  featuresList,
  setFeaturesList,
  downloadSections,
  setDownloadSections,
  mainDescription,
  setMainDescription,
  footerTitle,
  setFooterTitle,
  secondaryDescription,
  setSecondaryDescription,
}: {
  footerParagraphs: IGenericText[];
  featuresList: IFeature[];
  downloadSections: IDownloadSection[];
  setFooterParagraphs: React.Dispatch<React.SetStateAction<IGenericText[]>>;
  setFeaturesList: React.Dispatch<React.SetStateAction<IFeature[]>>;
  setDownloadSections: React.Dispatch<React.SetStateAction<IDownloadSection[]>>;
  mainDescription: string;
  setMainDescription: React.Dispatch<React.SetStateAction<string>>;
  footerTitle: string;
  setFooterTitle: React.Dispatch<React.SetStateAction<string>>;
  secondaryDescription: string;
  setSecondaryDescription: React.Dispatch<React.SetStateAction<string>>;
}) {
  return (
    <Form>
      <h5>Sees de Download</h5>
      <DownloadSections
        downloadSections={downloadSections}
        setDownloadSections={setDownloadSections}
      ></DownloadSections>
      <hr></hr>

      <h5>Sees de Caractersticas</h5>
      <FeaturesSection
        featuresList={featuresList}
        setFeaturesList={setFeaturesList}
      ></FeaturesSection>
      <hr></hr>

      <h5>Descries</h5>
      <Form.Group className="mb-3">
        <Form.Label>Descrio Principal</Form.Label>
        <Form.Control
          as="textarea"
          value={mainDescription}
          onChange={(e) => setMainDescription(e.target.value)}
        />
      </Form.Group>

      <Form.Group className="mb-5">
        <Form.Label>Descrio Secundria</Form.Label>
        <Form.Control
          as="textarea"
          value={secondaryDescription}
          onChange={(e) => setSecondaryDescription(e.target.value)}
        />
      </Form.Group>
      <hr></hr>

      <h5>Rodap</h5>
      <Form.Group className="mb-3">
        <Form.Label>Ttulo do rodap</Form.Label>
        <Form.Control
          as="input"
          value={footerTitle}
          onChange={(e) => setFooterTitle(e.target.value)}
        />
      </Form.Group>
      <FooterParagraphs
        footerParagraphs={footerParagraphs}
        setFooterParagraphs={setFooterParagraphs}
      ></FooterParagraphs>
    </Form>
  );
}
export default SimpleTemplateForm;
